@import url('https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400..700;1,400..700&family=Geologica:wght@100..900&display=swap');

body {
    margin: 0;
    padding: 0;
    background: black;
    color: rgb(142, 142, 147);

    font-family: "Geologica", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;

}

img {
    box-sizing: border-box;
    height: auto;
    transition: opacity .75s;
}

#root {
    display: flex;
    justify-content: center;
    height: 100vh;
}

.grid {
    display: grid;
    width: 100%;
    overflow: hidden;
    grid-template-columns: repeat(auto-fit, minmax(20%, 1fr));

    > img {
        opacity: .2;
        width: 100%;
        height: auto;
        object-fit: cover;

        &.bright {
            opacity: 1;
        }
    }

    &.loaded {
        > img {
            opacity: 1;
        }
    }
}

.recommendations {
    margin-bottom: 6rem;

    .track {
        display: flex;
        align-items: center;
        flex-direction: column;
        text-align: center;
        margin: 2rem;

        h3 {
            margin: .5rem;
            padding: 0;
            color: rgb(229, 229, 234);
        }

        p {
            margin: 0;
            padding: 0;
        }

        img {
            display: block;
            width: 90%;
            max-width: 320px;
            margin: 1rem;
        }
    }
}

.controls {
    position: fixed;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    bottom: 0;
    background: rgba(23, 23, 23, 0.47);
    backdrop-filter: blur(30px);
    width: 100%;
    padding: 1rem;

    @media all and (display-mode: standalone) {
        padding: 1rem 1rem 2rem;
    }
}

button {
    outline: none;
    border: none;
    border-radius: .25rem;
    padding: .75rem;
    background: transparent;
    color: inherit;
    font-weight: 700;
}

[data-tooltip] {
    position: relative;

    &:hover::before, &:focus::before, &:active::before {
        content: attr(data-tooltip);
        position: absolute;
        top: -2rem;
        font-weight: bold;
        left: 25%;
        transform: translateX(-20%);
        padding: .5rem;
        background: rgba(0, 0, 0, .75);
        color: white;
        border-radius: .25rem;
        font-size: .5rem;
    }
}
